import { useEffect } from "react";
import { useLocation } from "react-router-dom";
import AppLayout from "../components/AppLayout";
import { v4 as uuidv4 } from "uuid";
import routes from "../routes";
import { useSelector, useDispatch } from "react-redux";
import { RootState } from "../store/configureStore";
import { AppDispatch } from "../store/configureStore";
import { loadMyInfo } from "../reducers/user";
import { loadPosts } from "../reducers/post";
import PostForm from "../components/PostForm";
import PostCard from "../components/PostCard";

const Home: React.FC = () => {
  const location = useLocation(); //   location
  const title = process.env.REACT_APP_APP_TITLE; //  
  const { me } = useSelector((state: RootState) => state.user);
  const dispatch = useDispatch<AppDispatch>();
  // const { isLoggedIn } = useSelector((state: RootState) => state.user);
  const { mainPosts, hasMorePosts, loadPostsLoading, retweetError } =
    useSelector((state: RootState) => state.post);

  //     
  useEffect(() => {
    const route = routes.find((p) => p.path === location.pathname);
    if (route) {
      const { title: subTitle } = route;
      document.title = title + " - " + subTitle;
    }
  }, [location.pathname, title]);

  useEffect(() => {
    if (retweetError) {
      alert(retweetError);
    }
  }, [retweetError]);

  // const lastIdValue = mainPosts[mainPosts.length - 1]?.id;

  // useEffect(() => {
  //   dispatch(loadMyInfo());
  //   const lastId = {
  //     lastId: lastIdValue,
  //   } as PostLastId;

  //   if (hasMorePosts) {
  //     dispatch(loadPosts(lastId));
  //   }
  // }, [dispatch, lastIdValue, hasMorePosts]);

  const lastId = mainPosts[mainPosts.length - 1]?.id;

  useEffect(() => {
    dispatch(loadMyInfo());
    console.log(lastId, ``);

    //posts 
    if (hasMorePosts && !loadPostsLoading) {
      dispatch(loadPosts(lastId));
      console.log(lastId, ``);
    }
  }, [dispatch, lastId, hasMorePosts, loadPostsLoading]);

  //   window.addEventListener('scroll', onScroll);
  //   return () => {
  //     window.removeEventListener('scroll', onScroll);
  //   };
  // }, [hasMorePosts, loadPostsLoading, mainPosts]);

  return (
    <AppLayout>
      {me && <PostForm />}
      {mainPosts.map((post) => (
        <PostCard key={uuidv4()} post={post} images={post.Images} />
      ))}
    </AppLayout>
  );
};

export default Home;
