import IndexPage from "../pages/index";
import Signup from "../pages/signup";
import Profile from "../pages/profile";
import Follow from "../pages/follow";
import PostPage from "../pages/post";
import UserPage from "../pages/user";
import HashtagPage from "../pages/hashtag";

// import { Post } from "../types";

type MyType = {
  path: string;
  title?: string;
  element: React.ReactElement;
  auth?: boolean;
};

const routes: MyType[] = [
  {
    path: "/",
    title: "Home",
    element: <IndexPage />,
  },
  {
    path: "/signup",
    title: " ",
    element: <Signup />,
  },
  {
    path: "/profile",
    title: "",
    element: <Profile />,
    auth: true,
  },
  {
    path: "/follow",
    title: " ",
    element: <Follow />,
    auth: true,
  },
  {
    path: "/hashtag/:tag",
    title: "",
    element: <HashtagPage />,
  },
  {
    path: "/post/:id",
    title: "",
    element: <PostPage />,
  },
  {
    path: "/user/:id",
    title: " ",
    element: <UserPage />,
  },
  {
    path: "/user/:id/posts",
    title: " ",
    element: <UserPage />,
  },
];

export default routes;
