import { useEffect, useState, useCallback } from "react";
import routes from "../routes";
import useSWR from "swr";
import axios from "axios";
import { useLocation, useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { AppDispatch } from "../store/configureStore";
import { Container, Row } from "react-bootstrap";
import { RootState } from "../store/configureStore";
import { Post } from "../types";
import { loadMyInfo, follow, unfollow } from "../reducers/user";

import AppLayout from "../components/AppLayout";
import NickEditForm from "../components/NickEditForm";
import FollowList from "../components/FollowList";

interface ProfileProps {
  post?: Post;
}

const fetcher = (url: string) => axios.get(url).then((result) => result.data);

const Profile: React.FC<ProfileProps> = ({ post }) => {
  const location = useLocation(); //   location
  const id = useSelector((state: RootState) => state.user.me?.id);
  const dispatch = useDispatch<AppDispatch>();
  const navigate = useNavigate();
  const { me } = useSelector((state: RootState) => state.user);

  const [followersLimit, setFollowersLimit] = useState(3);
  const [followingsLimit, setFollowingsLimit] = useState(3);

  const { data: followersData, error: followerError } = useSWR(
    `http://localhost:8000/user/followers?limit=${followersLimit}`,
    fetcher
  );
  const { data: followingsData, error: followingError } = useSWR(
    `http://localhost:8000/user/followings?limit=${followingsLimit}`,
    fetcher
  );

  useEffect(() => {
    if (post) {
      const followsData = {
        postId: post.id,
        userId: id,
      };
      dispatch(follow(followsData));
      dispatch(unfollow(followsData));
    }
  }, [dispatch, post, id]);

  const title = process.env.REACT_APP_APP_TITLE; //  
  useEffect(() => {
    const route = routes.find((p) => p.path === location.pathname);
    if (route) {
      const { title: subTitle } = route;
      document.title = title + " - " + subTitle;
    }
  }, [location.pathname, title]);

  useEffect(() => {
    dispatch(loadMyInfo());
  }, [dispatch]);

  useEffect(() => {
    if (me && !me.id) {
      navigate("/");
    }
  }, [me, navigate]);

  const loadMoreFollowings = useCallback(() => {
    setFollowingsLimit((prev) => prev + 3);
  }, []);

  const loadMoreFollowers = useCallback(() => {
    setFollowersLimit((prev) => prev + 3);
  }, []);

  if (followerError || followingError) {
    console.error(followerError || followingError);
    return <div>   .  .</div>;
  }

  return (
    <div>
      <AppLayout>
        <Container>
          <Row>
            <NickEditForm />
            <FollowList
              header=" "
              data={followingsData}
              onClickMore={loadMoreFollowings}
              loading={!followingsData && !followingError}
            />
            <FollowList
              header=" "
              data={followersData}
              onClickMore={loadMoreFollowers}
              loading={!followersData && !followerError}
            />
          </Row>
        </Container>
      </AppLayout>
    </div>
  );
};

export default Profile;
