import { useEffect } from "react";
import { useParams } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { AppDispatch } from "../../store/configureStore";
import { RootState } from "../../store/configureStore";
import { loadMyInfo } from "../../reducers/user";
import { loadPost } from "../../reducers/post";
import AppLayout from "../../components/AppLayout";
import PostCard from "../../components/PostCard";
import { Post } from "../../types";
import { Helmet } from "react-helmet";

const PostPage = () => {
  const { id } = useParams<{ id: string }>();
  const postId = id ? parseInt(id) : undefined;
  const dispatch = useDispatch<AppDispatch>();
  const { singlePost } = useSelector((state: RootState) => state.post);

  useEffect(() => {
    if (postId !== undefined) {
      dispatch(loadPost(postId));
      dispatch(loadMyInfo());
    }
  }, [dispatch, postId]);

  // singlePost null  ,
  // null   .
  if (!singlePost) {
    return <div>  .</div>;
  }

  return (
    <div>
      <AppLayout>
        <Helmet>
          <title>
            {singlePost.User?.nick}
             
          </title>
          <meta name="description" content={singlePost.content} />
          <meta
            property="og:title"
            content={`${singlePost.User?.nick} `}
          />
          <meta property="og:description" content={singlePost.content} />
          {/* <meta
            property="og:image"
            content={
              singlePost.Images[0]
                ? singlePost.Images[0].src
                : "https://nora.com/favicon.ico"
            }
          />
          <meta property="og:url" content={`https://nora.com/post/${id}`} /> */}
        </Helmet>
        <PostCard post={singlePost as Post} />
      </AppLayout>
    </div>
  );
};

export default PostPage;
