import React, { useRef, useCallback, useState, useEffect } from "react";
import { v4 as uuidv4 } from "uuid";
import { Form, Button } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "../store/configureStore";
import styled from "styled-components";
import postSlice, { addPost, uploadImage } from "../reducers/post"; //  .
import { AppDispatch } from "../store/configureStore";
import "../styles/Post.scss";

const FormWrapper = styled(Form)`
  margin: 10px 0 20px;
  width: 100%;
`;

const FormTextarea = styled(Form.Control)`
  resize: none;
  width: 100%;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  height: 10vh;
`;

const PostForm: React.FC = () => {
  const { imagePaths, addPostDone } = useSelector(
    (state: RootState) => state.post
  );
  const id = useSelector((state: RootState) => state.user.me?.id);
  const [text, setText] = useState<string>("");
  const dispatch = useDispatch<AppDispatch>();
  const imageInput = useRef<HTMLInputElement>(null);

  const onClickImageUpload = useCallback(() => {
    if (imageInput.current) {
      imageInput.current.click();
    }
  }, []);

  useEffect(() => {
    if (addPostDone) {
      setText("");
    }
  }, [addPostDone]);

  const onChangeText = useCallback(
    (e: React.ChangeEvent<HTMLTextAreaElement>) => {
      setText(e.target.value);
    },
    []
  );

  // const onSubmit = useCallback(
  //   (e: React.SyntheticEvent) => {
  //     e.preventDefault();
  //     const postData: PostText = {
  //       content: text,
  //       userId: id,
  //     };
  //     dispatch(addPost(postData));
  //     setText("");
  //   },
  //   [dispatch, text, id]
  // );

  const onSubmit = useCallback(() => {
    if (!text || !text.trim()) {
      return alert(" .");
    }
    const formData = new FormData();
    // multer  none   formData  .
    imagePaths.forEach((p) => {
      formData.append("image", p);
    });
    formData.append("content", text);
    formData.append("userId", (id || 0).toString());
    // formData   append    !!

    console.log("onsubmitdata", formData);
    return dispatch(addPost(formData));
  }, [dispatch, imagePaths, text, id]);

  const onChangeImages = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      console.log("images", e.target.files);
      const imageFormData = new FormData();
      [].forEach.call(e.target.files, (f) => {
        imageFormData.append("image", f);
      });
      dispatch(uploadImage(imageFormData));
    },
    [dispatch]
  );

  const onRemoveImage = useCallback(
    //  
    (index: number) => () => {
      dispatch(postSlice.actions.removeImage(index));
    },
    [dispatch]
  );

  return (
    <FormWrapper encType="multipart/form-data" onSubmit={onSubmit}>
      <Form.Group>
        <FormTextarea
          as="textarea"
          value={text}
          className="post-textarea"
          onChange={onChangeText}
          maxLength={140}
          placeholder=" .."
        />
      </Form.Group>
      <div>
        <input
          type="file"
          name="image"
          multiple
          hidden
          ref={imageInput}
          onChange={onChangeImages}
        />
        <Button onClick={onClickImageUpload}> </Button>
        <Button type="submit" className="post-button">
          
        </Button>
      </div>
      <div>
        {imagePaths.map((filename, i) => (
          <div key={uuidv4()} style={{ display: "inline-block" }}>
            <img
              src={`http://localhost:8000/${filename}`}
              style={{ width: "200px" }}
              alt={filename}
            />
            <div>
              <Button onClick={onRemoveImage(i)}></Button>
              {/* map   ,   */}
            </div>
          </div>
        ))}
      </div>
    </FormWrapper>
  );
};

export default PostForm;
